#insta-feed {
  display: grid;
  grid-template-columns: repeat(4, 16.5rem);
  grid-auto-flow: column;
  grid-column-gap: 1rem;
  overflow: auto;
  padding: 0 1.25rem;

  &::-webkit-scrollbar {
    width: 0;
    height: 0;
  }

  .instafeed-container {
    width: 100% !important;
    padding-top: 100% !important;

    img {
      width: 100% !important;
      height: 100% !important;
    }
  }

  .instafeed-overlay {
    width: 100% !important;
    height: 100% !important;
  }

  a {
    z-index: 1;

    &:nth-child(9){
      display: none;
    }
  }

  @include t-media-query($s-medium-up) {
    grid-template-columns: repeat(4, 1fr);
    grid-auto-flow: column;
    padding: 0;
  }
}
