.c-instagram-feed {
  background: $s-color--background-subdued;
  text-align: center;

  &__outer {
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
  }

  &__inner {
    padding: 2.3125rem 0 3rem;
    overflow: hidden;
  }

  &__title {
    margin-bottom: 0.8125rem;
  }

  &__ig-handle {
    color: $s-color--font-focus;
  }

  &__feed-container {
    min-height: 20.1875rem;
  }

  #insta-feed {
    margin-top: 2.6875rem;
    grid-template-columns: repeat(4, 17.5rem);
    grid-column-gap: 1.25rem;
    overflow: scroll;
    padding: 2.1875rem 0 0 1.25rem !important;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: none;
    scrollbar-color: $s-color--background-subdued $s-color--background-subdued;

    &::-webkit-scrollbar {
      width: 0;
      height: 0;
      display: none;
    }

    &::-webkit-scrollbar-track {
      background: $s-color--background-subdued;
      height: 0;
      width: 0;
    }

    &::-webkit-scrollbar-thumb {
      background: $s-color--background-subdued;
      height: 0;
      width: 0;
    }

    a:nth-child(3),
    a:nth-child(7) {
      margin-top: -2.1875rem;
    }

    &:after{
        content:"";
        width: 0.0625rem;
        height: 0.0625rem;
        display: block;
      }
  }

  @include t-media-query($s-small-up) {
    &__ig-handle {
      margin-bottom: 4.375rem;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      padding: 8rem 6% 7rem;
    }

    &__title {
      margin-bottom: 0.625rem;
    }

    &__ig-handle {
      text-align: left;
      margin-bottom: 0;
    }

    &__header {
      position: absolute;
    }

    #insta-feed {
      grid-column-gap: 2.45%;
      grid-template-columns: repeat(4, 1fr);
      padding: 6.25rem 0 0 !important;
      margin-top: 0;

      a:nth-child(3),
      a:nth-child(7) {
        margin-top: -6.25rem;
      }

      a:nth-child(5) {
        margin-top: -2.8125rem;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding: 12.5rem $s-gutter--edge-large 7rem;
    }

    #insta-feed {
      padding: 8.4375rem 0 0 !important;
      a:nth-child(3),
      a:nth-child(7) {
        margin-top: -8.4375rem;
      }

      a:nth-child(5) {
        margin-top: -2.375rem;
      }
    }
  }
}
